<template>
  <div>
    <h1>注册界面</h1>
    用户名:<el-input
      placeholder="请输入用户名,仅限英文和字母组合"
      v-model="username"
      clearable>

    </el-input>
    <br>
    <br>

    密码:
    <el-input
      placeholder="请输入密码"
      v-model="password"
      show-password>
    </el-input>
    <br>
    <br>
    确认密码:
    <el-input
      placeholder="请再次输入密码"
      v-model="rePassword"
      show-password>
    </el-input>
    <br>
    <br>
    昵称:<el-input
      placeholder="请输入昵称"
      v-model="nickname"
      clearable>

    </el-input>

    <el-button type="primary"
      plain id="register"
      @click="register">注册
    </el-button>

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'MyRegister',
  data() {
    return {
      username: '',
      password: '',
      rePassword: '',
      nickname: '',
    }
  },

  methods: {
    register() {
      let regx = /^[A-Za-z0-9]$/
      if (regx.test(this.username)) {
        if (this.rePassword !== this.password) {
          alert('密码不一致,请重新输入')
          this.password = ''
          this.rePassword = ''
        } else {
          axios({
            method: 'post',
            url: 'http://121.40.113.226:7778/user/register',
            data: {
              username: this.username,
              password: this.password,
              nickname: this.nickname,
            },
          }).then((res) => {
            if (res.data.status == 200) {
              alert('注册成功,快去登录吧')
              this.$router.push('/login')
            }
          })
        }
      }else{
        alert('用户名不符合规则')
        this.username = ''
      }
    },
  },
}
</script>
<style>
#register {
  position: absolute;
  left: 9.5em;
  width: 8em;
  bottom: 10em;
}
</style>